.formCreateNote {
    margin: 10px auto;
    width: 95%;
    max-width: 700px;
    display: grid;
    grid-template-columns: 1fr;


}

.buttoncreate {
    background: #1f00cc;
}

select {
    width: 100%;
    padding: 5px;
    font-size: 18px;
    margin: 5px auto;
    background: #fff;
    border: 1px solid #ccc;
    border-radius: 5px;
}

.black {
    color: #000;
}

textarea {
    font-family: 'Raleway', sans-serif;
    resize: vertical;
    min-height: 50px;
}

.react-datepicker-wrapper {
    width: 100%;
    margin-top: -4px;
}
