footer {
    background: #000;
    padding: 5px 5px 5px 10%;
    color: #fff;
    position: fixed;
    bottom: 0;
    width: 100%;
    box-sizing: border-box;
}

.footer1 {
    display: block;
}

.footernone {
    display: none;
}

@media (max-width: 600px) {
    .footer1 {
        display: none
    }
    
    .footernone {
        display: block;
        display: grid;
        background: #000;
        padding: 2px;
        font-size: 7px;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        text-align: center;
    }

    .iconfooter {
        width: 40%;
        display: block;
        margin: 0 auto;
    }

    
  }