form, .second {
    padding: 10px;
    background: #fff;
    border-radius: 10px;
    font-size: 24px;
    box-shadow: 5px 7px 5px #ccc;
}

.second {
    grid-column: 2 / -1 ;
}

.text {
    width: 100%;
    margin: 7px auto;
    padding: 5px;
    border-radius: 5px;
    box-sizing: border-box;
    font-size: 18px;
    border: 1px solid #ccc;
}

ul {
    list-style: none;
}

.main {
    width: 95%;
    max-width: 1000px;
    margin: 10px auto;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 10px;
    align-items: baseline;
}

.button {
    width: 100%;
    padding: 5px;
    font-size: 20px;
    border-radius: 5px;
    background: rgb(71, 145, 77);
    color: #fff;
}

.button:hover {
    color: #fff;
    background: #000;
}

@media (max-width: 600px) {
    .main {
        grid-template-columns: 1fr;
    }

    .second {
        grid-column: 1 / 2;
    }
  }