header {
    background: #000;
    color: #fff;
    padding: 0px 10% 0px 10%;
}

h1, h2 {
    display: inline-block;
}

h2 {
    padding: 5px 10px 5px 10px;
    text-decoration: none;
    color: #fff;
}

.nav {
    float: right;
}

@media (max-width: 600px) {
    h1 {
        font-size: 38px;
    }

    .nav {
        display: none;
    }
  }