.grid-main {
    width: 90%;
    max-width: 1000px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    margin: 10px auto;
    grid-gap: 15px;
    margin-bottom: 50px;
}

.grid-notes {
    background: #fff;
    padding: 10px;
    border-radius: 5px;
    box-shadow: 5px 5px 2px #ccc;
    font-size: 20px;
    display: flex;
    flex-direction: column;
}

.title {
    font-size: 24px;
    display: inline;
}

.content {
    color: #555;
    height: 100%;
}

.icon {
    width: 25px;
    margin: 0px 3px;
    float: right;
    cursor: pointer;
}

.block {
    display: inline;

}

.none {
    display: none;
}

@media (max-width: 600px) {
    .grid-main {
        grid-template-columns: 1fr 1fr;
        margin: 5px auto;
        grid-gap: 5px;
        width: 99%;
        margin-bottom: 100px;
    }

    .grid-notes {
        background: #fff;
        padding: 5px;
        border-radius: 5px;
        font-size: 18px;
    }

    .title {
        font-size: 20px;
    }

    .check {
        display: none;
    }

    .none {
        display: block;
        padding: 2px;
        font-size: 12px;
    }
  }